import React, { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import DefaultPage from "../components/DefaultPage";
import { ReactComponent as ProfileIcon } from "../../assets/icons/profile.svg";
import { ReactComponent as ReviewsIcon } from "../../assets/icons/star.svg";
import {
  Button,
  Checkbox,
  CircularProgress,
  createMuiTheme,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormLabel,
  TextField,
  ThemeProvider,
} from "@material-ui/core";
import { blue } from "@material-ui/core/colors";
import fire from "../../fire";
import { Map, Placemark, YMaps } from "react-yandex-maps";
import { storage } from "../../fire";
import { connect } from "react-redux";
import { SET_USER } from "../../redux/types";
import { history } from "../../history";

function Profile({ user }) {
  const [email, setEmail] = useState("");
  const [loading, setLoading] = useState(false);
  const [carwashName, setCarwashName] = useState("");
  const [phone, setPhone] = useState("");
  const [legalAddress, setLegalAddress] = useState("");
  const [type, setType] = useState("");
  const [bin, setBin] = useState("");
  const [avatarImg, setAvatarImg] = useState(null);
  const [progress, setProgress] = useState(null);
  const avatar = useRef();
  const [coords, setCoords] = useState([43.238352, 76.8958813]);
  const [services, setServices] = useState([
    "Free Wifi",
    "Cashless Payment",
    "Waiting Room",
    "Cafe",
  ]);
  const [selectedServices, setSelectedServices] = useState([]);
  const theme = createMuiTheme({
    palette: {
      primary: blue,
    },
  });

  useEffect(() => {
    const userId = localStorage.getItem("userId");
    const currentUser = fire.auth().currentUser;
    currentUser == null && history.push("/login");
    if (currentUser || userId) {
      const ref = fire
        .firestore()
        .collection("users")
        .doc(currentUser?.uid || userId);
      console.log(currentUser?.uid, userId);
      console.log(currentUser, userId);
      setLoading(true);
      ref.get().then((value) => {
        console.log(value.data());
        setEmail(value.data()?.email ?? "");
        setPhone(value.data()?.phone ?? "");
        setBin(value.data()?.bin ?? "");
        setType(value.data()?.type ?? "");
      });
      fire
        .firestore()
        .collection("car_wash")
        .doc(currentUser?.uid || userId)
        .get()
        .then((value) => {
          console.log(value.data());
          setCarwashName(value.data()?.name ?? "");
          setCoords(value.data()?.location ?? "");
          setLegalAddress(value.data()?.legalAddress ?? "");
          setSelectedServices(value.data()?.additionalServices ?? []);
          setLoading(false);
        });
      storage
        .ref("images/car_washs")
        .child(`${currentUser?.uid || userId}.png`)
        .getDownloadURL()
        .then((url) => {
          setAvatarImg(url);
        });
    } else {
      history.push("/login");
    }
  }, []);

  const handleImageChange = (event) => {
    if (event.target.files[0]) {
      console.log(event.target.files[0]);
      setAvatarImg(event.target.files[0]);
    }
    const userId = localStorage.getItem("userId") ?? user.uid;
    console.log(`images/car_washs/${userId}.png`);
    const uploadTask = storage
      .ref(
        `images/car_washs/${userId}.${event.target.files[0].name.split(".")[1]}`
      )
      .put(event.target.files[0], {
        contentType: "image/png",
      });
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progress);
      },
      (error) => {
        console.log(error);
      },
      () => {
        storage
          .ref("images/car_washs")
          .child(`${userId}.png`)
          .getDownloadURL()
          .then((url) => {
            setAvatarImg(url);
            setProgress(null);
          });
      }
    );
  };

  return (
    <DefaultPage>
      <div className="cabinet__profile" style={{ display: "flex" }}>
        <div className="tabs">
          <Link to="/cabinet/profile" style={{ textDecoration: "none" }}>
            <div className="tab-item active">
              <ProfileIcon />
              <p>Profile</p>
            </div>
          </Link>
          <Link to="/cabinet/reviews" style={{ textDecoration: "none" }}>
            <div className="tab-item">
              <ReviewsIcon />
              <p>RATING AND REVIEWS</p>
            </div>
          </Link>
        </div>
        {loading ? (
          <CircularProgress />
        ) : (
          <div className="content">
            <div className="title">PROFILE</div>
            <div className="profile_img">
              {progress ? <progress value={progress} max="100" /> : null}
              <input
                onChange={handleImageChange}
                accept="image/png"
                type="file"
                id="profile_img"
                ref={avatar}
              />
              <img
                src={
                  avatarImg ??
                  "data:image/png;base64,"
                }
                alt=""
                onClick={() => {
                  console.log("tets");
                  avatar?.current?.click();
                }}
              />
            </div>
            <div className="form_field">
              <p>Car wash legal name: </p>
              <TextField
                id="outlined-basic"
                variant="outlined"
                value={carwashName}
                onChange={(e) => setCarwashName(e.target.value)}
              />
            </div>
            <div className="form_field">
              <p>Email: </p>
              <TextField
                id="outlined-basic"
                variant="outlined"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className="form_field">
              <p>Contract number: </p>
              <TextField
                id="outlined-basic"
                variant="outlined"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="form_field">
              <p>A type: </p>
              <TextField
                id="outlined-basic"
                variant="outlined"
                value={type}
                onChange={(e) => setType(e.target.value)}
              />
            </div>
            <div className="form_field">
              <p>Legal address: </p>
              <TextField
                id="outlined-basic"
                variant="outlined"
                value={legalAddress}
                onChange={(e) => setLegalAddress(e.target.value)}
              />
            </div>
            <div className="form_field">
              <p>INN / EDRPOU:: </p>
              <TextField
                id="outlined-basic"
                variant="outlined"
                value={bin}
                onChange={(e) => setBin(e.target.value)}
              />
            </div>
            <div className="maps_services">
              <div className="map" style={{ width: "50%" }}>
                <YMaps>
                  <Map
                    instanceRef={(ref) => {
                      if (ref) {
                        ref.events.add("click", function (e) {
                          console.log(e.get("coords"));
                          setCoords(e.get("coords"));
                        });
                      }
                    }}
                    defaultState={{
                      center: [43.238352, 76.8958813, 17],
                      zoom: 10,
                    }}
                  >
                    <Placemark geometry={coords} />
                  </Map>
                </YMaps>
              </div>
              <div className="services" style={{ width: "50%" }}>
                <FormControl component="fieldset">
                  <FormLabel component="legend">Additional Services</FormLabel>
                  <FormGroup>
                    {services.map((service) => {
                      return (
                        <FormControlLabel
                          control={
                            <Checkbox
                              checked={selectedServices.includes(service)}
                              onChange={() => {
                                console.log(service, selectedServices);
                                setSelectedServices([
                                  ...selectedServices,
                                  ...[service],
                                ]);
                              }}
                              name={service}
                            />
                          }
                          label={service}
                        />
                      );
                    })}
                  </FormGroup>
                </FormControl>
              </div>
            </div>
            <ThemeProvider theme={theme}>
              <Button
                variant="contained"
                color="primary"
                type="submit"
                onClick={() => {
                  const currentUser = fire.auth().currentUser;
                  const userId = localStorage.getItem("userId");
                  const ref = fire
                    .firestore()
                    .collection("users")
                    .doc(currentUser?.uid || userId);
                  const ref1 = fire
                    .firestore()
                    .collection("car_wash")
                    .doc(currentUser?.uid || userId);
                  fire
                    .firestore()
                    .collection("car_wash")
                    .doc(currentUser?.uid || userId)
                    .get()
                    .then((value) => {
                      ref1.set({
                        ...(value.data() ?? {}),
                        name: carwashName,
                        location: coords,
                        legalAddress: legalAddress,
                        additionalServices: selectedServices,
                      });
                    });

                  setLoading(true);
                  ref.get().then((value) => {
                    console.log({
                      ...(value.data() ?? {}),
                      email: email,
                      phone: phone,
                      bin: bin,
                      legalAddress: legalAddress,
                      additionalServices: selectedServices,
                      type: type,
                    });
                    const docSnapshot = ref
                      .set({
                        ...(value.data() ?? {}),
                        email: email,
                        phone: phone,
                        bin: bin,

                        type: type,
                      })
                      .then((response) => {
                        console.log(response);
                        setLoading(false);
                      });
                  });
                }}
              >
                Save
              </Button>
            </ThemeProvider>
          </div>
        )}
      </div>
    </DefaultPage>
  );
}
const mapStateToProps = (state) => {
  return {
    user: state.authReducer.user,
  };
};

const mapDispatchToProps = (dispatch) => ({
  setUser: (payload) => dispatch({ type: SET_USER, payload: payload }),
});

export default connect(mapStateToProps, mapDispatchToProps)(Profile);
